import { Loading } from 'notiflix/build/notiflix-loading-aio';

export const addSpinner = function addSpinner() {
  Loading.custom('Loading your movies...', {
    customSvgCode: '<svg id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 400 400"><image width="400" height="400" xlink:href="data:image/gif;base64,"/></svg>',
    // svgColor: '$color-accent',
    // backgroundColor: '$color-overlay-modal',
    svgSize: '100px',
  });
};

export const removeSpinner = function removeSpinner() {
  Loading.remove();
};